<template>
  <view class="invite-page" :style="{ paddingTop: containerPaddingTop }">
    <!-- Ëá™ÂÆö‰πâÂØºËà™Ê†è -->
    <view class="custom-navbar">
      <!-- Ê∏êÂèòËÉåÊôØ -->
      <view class="navbar-gradient-bg"></view>
      <!-- Áä∂ÊÄÅÊ†èÂç†‰Ωç -->
      <view class="status-bar" :style="{ height: statusBarHeight + 'px' }"></view>
      <!-- ÂØºËà™Ê†èÂÜÖÂÆπ -->
      <view class="navbar-content" :style="{ height: navBarHeight + 'px' }">
        <view class="navbar-left" @click="goBack">
          <text class="back-icon">‚Üê</text>
        </view>
        <view class="navbar-title">
          <text class="title-text">{{ isInviteMode ? 'Êé•ÂèóÈÇÄËØ∑' : 'ÈÇÄËØ∑Âè¶‰∏ÄÂçä' }}</text>
        </view>
        <view class="navbar-right"></view>
      </view>
    </view>

    <!-- ÂÜÖÂÆπÂå∫Âüü -->
    <view class="content-area">
      <!-- Êé•ÂèóÈÇÄËØ∑Ê®°ÂºèÔºöÊòæÁ§∫ÈÇÄËØ∑Êñπ‰ø°ÊÅØ -->
      <view v-if="isInviteMode" class="invite-container">
        <view class="invite-header">
          <text class="invite-title">üíï Êî∂Âà∞ÈÇÄËØ∑</text>
          <text class="invite-subtitle">{{ creatorInfo.nickName || 'Â•ΩÂèã' }} ÈÇÄËØ∑‰Ω†Êàê‰∏∫ÊÉÖ‰æ£</text>
        </view>

        <!-- ÈÇÄËØ∑Êñπ‰ø°ÊÅØÂç°Áâá -->
        <view class="creator-card glass-card">
          <image class="creator-avatar" :src="creatorInfo.avatarUrl || '/static/login/love.jpg'" mode="aspectFill" />
          <text class="creator-name">{{ creatorInfo.nickName || 'Â•ΩÂèã' }}</text>
          <text class="creator-tip">ÊÉ≥Ë¶ÅÂíå‰Ω†Âª∫Á´ãÊÉÖ‰æ£ÂÖ≥Á≥ª</text>
        </view>

        <!-- Êé•ÂèóÊåâÈíÆ -->
        <view class="action-buttons">
          <button class="btn accept-btn" @click="handleAccept" :disabled="isAccepting">
            {{ isAccepting ? 'Êé•Âèó‰∏≠...' : 'Êé•ÂèóÈÇÄËØ∑' }}
          </button>
          <button class="btn cancel-btn" @click="goBack">ÊãíÁªù</button>
        </view>

        <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
        <view v-if="isAccepting" class="loading-tip">
          <text>Ê≠£Âú®Âª∫Á´ãÂÖ≥Á≥ª...</text>
        </view>
      </view>

      <!-- ÂèëËµ∑ÈÇÄËØ∑Ê®°ÂºèÔºöÁîüÊàêÈÇÄËØ∑Á†ÅÂπ∂ÂàÜ‰∫´ -->
      <view v-else class="create-container">
        <!-- Â∑≤ÁªëÂÆöÁä∂ÊÄÅ -->
        <view v-if="isBound" class="bound-status">
          <view class="bound-header">
            <text class="bound-icon">üíë</text>
            <text class="bound-title">Â∑≤ÁªëÂÆö</text>
          </view>
          <view class="partner-card glass-card">
            <image class="partner-avatar" :src="partnerInfo.displayAvatar || partnerInfo.avatarUrl || '/static/login/love.jpg'" mode="aspectFill" />
            <text class="partner-name">{{ partnerInfo.displayName || partnerInfo.nickName || 'ÂØπÊñπ' }}</text>
            <text class="bound-time">ÁªëÂÆö‰∫é {{ bindTimeText }}</text>
          </view>
          <button class="btn unbind-btn" @click="handleUnbind">Ëß£Èô§ÂÖ≥Á≥ª</button>
        </view>

        <!-- Êú™ÁªëÂÆöÁä∂ÊÄÅÔºöÁîüÊàêÈÇÄËØ∑ÊàñËæìÂÖ•ÈÇÄËØ∑Á†Å -->
        <view v-else class="invite-content">
          <!-- ËæìÂÖ•ÈÇÄËØ∑Á†ÅÊ®°Âºè -->
          <view v-if="showInputCode" class="input-code-container">
            <view class="invite-header">
              <text class="invite-title">üíï ËæìÂÖ•ÈÇÄËØ∑Á†Å</text>
              <text class="invite-subtitle">ËØ∑ËæìÂÖ•ÂØπÊñπÂàÜ‰∫´ÁöÑÈÇÄËØ∑Á†Å</text>
            </view>

            <view class="input-code-card glass-card">
              <input 
                class="code-input" 
                type="text" 
                v-model="inputCode" 
                placeholder="ËØ∑ËæìÂÖ•6‰ΩçÈÇÄËØ∑Á†Å"
                maxlength="6"
                :focus="inputFocus"
                @input="onInputCode"
              />
              <view class="input-actions">
                <button class="btn verify-btn" @click="verifyInputCode" :disabled="!inputCode || inputCode.length !== 6 || isVerifying">
                  {{ isVerifying ? 'È™åËØÅ‰∏≠...' : 'È™åËØÅÈÇÄËØ∑Á†Å' }}
                </button>
                <button class="btn cancel-input-btn" @click="cancelInputCode">ÂèñÊ∂à</button>
              </view>
            </view>
          </view>

          <!-- ÁîüÊàêÈÇÄËØ∑Á†ÅÊ®°Âºè -->
          <view v-else>
            <view class="invite-header">
              <text class="invite-title">üíï ÈÇÄËØ∑Âè¶‰∏ÄÂçä</text>
              <text class="invite-subtitle">ÂàÜ‰∫´ÈÇÄËØ∑ÁªôTAÔºå‰∏ÄËµ∑ËÆ∞ÂΩïÁæéÂ•ΩÊó∂ÂÖâ</text>
            </view>

            <!-- ÈÇÄËØ∑Á†ÅÊòæÁ§∫ -->
            <view v-if="inviteCode" class="invite-code-card glass-card">
              <text class="code-label">ÈÇÄËØ∑Á†Å</text>
              <view class="code-display">
                <text class="code-text">{{ inviteCode }}</text>
                <button class="copy-btn" @click="copyInviteCode">Â§çÂà∂</button>
              </view>
              <text class="code-tip">ÊúâÊïàÊúüÔºö{{ expireTimeText }}</text>
            </view>

            <!-- Êìç‰ΩúÊåâÈíÆ -->
            <view class="action-buttons">
              <button v-if="!inviteCode" class="btn primary-btn" @click="generateInviteCode" :disabled="isGenerating">
                {{ isGenerating ? 'ÁîüÊàê‰∏≠...' : 'ÁîüÊàêÈÇÄËØ∑Á†Å' }}
              </button>
              <button v-else class="btn primary-btn" @click="shareInvite" :disabled="isSharing">
                {{ isSharing ? 'ÂàÜ‰∫´‰∏≠...' : 'ÂàÜ‰∫´ÁªôTA' }}
              </button>
              <button v-if="inviteCode" class="btn secondary-btn" @click="regenerateInviteCode" :disabled="isGenerating">
                ÈáçÊñ∞ÁîüÊàê
              </button>
              <!-- ËæìÂÖ•ÈÇÄËØ∑Á†ÅÊåâÈíÆ -->
              <button class="btn input-code-btn" @click="showInputCodePanel">
                <text class="input-code-icon">üì•</text>
                <text>ËæìÂÖ•ÈÇÄËØ∑Á†Å</text>
              </button>
            </view>

            <!-- ÂàÜ‰∫´ÊèêÁ§∫ -->
            <view v-if="inviteCode" class="share-tip">
              <text class="tip-text">üí° ÁÇπÂáªÂè≥‰∏äËßí"..."ÊåâÈíÆÔºåÈÄâÊã©"ËΩ¨Âèë"ÂàÜ‰∫´ÁªôÂ•ΩÂèã</text>
            </view>
          </view>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import { createInviteCode, validateInviteCode, acceptInvite, getCoupleStatus, unbindCouple } from '../../api/couple.js';
import { saveCoupleInfo, getCoupleInfo, clearCoupleInfo, isBound as checkIsBound, getPartnerInfo } from '../../utils/couple.js';

export default {
  data() {
    return {
      statusBarHeight: 0,
      navBarHeight: 54,
      screenWidth: 375,
      // ÈÇÄËØ∑Á†ÅÁõ∏ÂÖ≥
      inviteCode: '',
      expireAt: '',
      isGenerating: false,
      isSharing: false,
      // Êé•ÂèóÈÇÄËØ∑Áõ∏ÂÖ≥
      isInviteMode: false, // ÊòØÂê¶‰∏∫Êé•ÂèóÈÇÄËØ∑Ê®°Âºè
      creatorInfo: {}, // ÈÇÄËØ∑Êñπ‰ø°ÊÅØ
      isAccepting: false,
      // ËæìÂÖ•ÈÇÄËØ∑Á†ÅÁõ∏ÂÖ≥
      showInputCode: false, // ÊòØÂê¶ÊòæÁ§∫ËæìÂÖ•ÈÇÄËØ∑Á†ÅÁïåÈù¢
      inputCode: '', // ËæìÂÖ•ÁöÑÈÇÄËØ∑Á†Å
      inputFocus: false, // ËæìÂÖ•Ê°ÜÊòØÂê¶ËÅöÁÑ¶
      isVerifying: false, // ÊòØÂê¶Ê≠£Âú®È™åËØÅÈÇÄËØ∑Á†Å
      // ÁªëÂÆöÁä∂ÊÄÅ
      isBound: false,
      partnerInfo: {},
      bindTime: ''
    };
  },
  computed: {
    containerPaddingTop() {
      const totalHeightPx = this.statusBarHeight + this.navBarHeight;
      const pxToRpx = 750 / this.screenWidth;
      const totalHeightRpx = totalHeightPx * pxToRpx;
      return totalHeightRpx + 20 + 'rpx';
    },
    expireTimeText() {
      if (!this.expireAt) return '';
      try {
        const date = new Date(this.expireAt);
        return date.toLocaleString('zh-CN', { 
          month: '2-digit', 
          day: '2-digit', 
          hour: '2-digit', 
          minute: '2-digit' 
        });
      } catch (e) {
        return '';
      }
    },
    bindTimeText() {
      if (!this.bindTime) return '';
      try {
        const date = new Date(this.bindTime);
        return date.toLocaleString('zh-CN', { 
          year: 'numeric',
          month: '2-digit', 
          day: '2-digit'
        });
      } catch (e) {
        return '';
      }
    }
  },
  onLoad(options) {
    this.getSystemInfo();
    
    // Ê£ÄÊü•ÊòØÂê¶ÊúâÈÇÄËØ∑Á†ÅÂèÇÊï∞Ôºà‰ªéÂàÜ‰∫´ËøõÂÖ•Ôºâ
    if (options.code) {
      this.handleInviteCode(options.code);
    } else {
      // Ê≠£Â∏∏ËøõÂÖ•ÔºåÊ£ÄÊü•ÁªëÂÆöÁä∂ÊÄÅ
      this.checkCoupleStatus();
    }
  },
  onShow() {
    // ÊØèÊ¨°ÊòæÁ§∫È°µÈù¢Êó∂Ê£ÄÊü•ÁªëÂÆöÁä∂ÊÄÅÔºà‰ªéÂÖ∂‰ªñÈ°µÈù¢ËøîÂõûÊó∂Ôºâ
    if (!this.isInviteMode) {
      this.checkCoupleStatus();
    }
  },
  // ÂæÆ‰ø°ÂàÜ‰∫´ÈÖçÁΩÆ
  onShareAppMessage() {
    if (!this.inviteCode) {
      return {
        title: 'ÈÇÄËØ∑‰Ω†Êàê‰∏∫ÊÉÖ‰æ£',
        path: '/pages/invite/index'
      };
    }
    
    const userInfo = this.getCurrentUserInfo();
    return {
      title: `${userInfo.nickName || 'Êàë'} ÈÇÄËØ∑‰Ω†Êàê‰∏∫ÊÉÖ‰æ£`,
      path: `/pages/invite/index?code=${this.inviteCode}`,
      imageUrl: '/static/invite-share.png' // ÂèØ‰ª•Ê∑ªÂä†‰∏Ä‰∏™ÂàÜ‰∫´ÂõæÁâá
    };
  },
  methods: {
    isMsgCodeSuccess(response) {
      return response && typeof response === 'object' && response.code === 200 && (response.msg || response.message);
    },
    getResponseMessage(response, fallbackText = '') {
      if (!response || typeof response !== 'object') {
        return fallbackText;
      }
      return response.msg || response.message || fallbackText;
    },
    getSystemInfo() {
      const systemInfo = uni.getSystemInfoSync();
      this.statusBarHeight = systemInfo.statusBarHeight || 0;
      this.screenWidth = systemInfo.windowWidth || 375;
      // #ifdef MP-WEIXIN
      this.navBarHeight = 54;
      // #endif
      // #ifdef H5
      this.navBarHeight = 54;
      // #endif
    },
    
    getCurrentUserInfo() {
      try {
        const loginInfo = uni.getStorageSync('login_info');
        return loginInfo && loginInfo.userInfo ? loginInfo.userInfo : {};
      } catch (e) {
        return {};
      }
    },
    
    // Ê£ÄÊü•ÁªëÂÆöÁä∂ÊÄÅ
    async checkCoupleStatus() {
      try {
        // ÂÖàÊ£ÄÊü•Êú¨Âú∞
        const localCoupleInfo = getCoupleInfo();
        if (localCoupleInfo && localCoupleInfo.isBound) {
          this.isBound = true;
          this.partnerInfo = localCoupleInfo.partnerInfo || {};
          this.bindTime = localCoupleInfo.bindTime || '';
          
          // ÂêåÊó∂Êõ¥Êñ∞Êú¨Âú∞ÈÇÄËØ∑Á†ÅÔºàÂ¶ÇÊûúÊúâÔºâ
          if (localCoupleInfo.inviteCode) {
            this.inviteCode = localCoupleInfo.inviteCode;
            this.expireAt = localCoupleInfo.inviteCodeExpire || '';
          }
          
          // ÂÜç‰ªéÊúçÂä°Âô®ÂêåÊ≠•‰∏ÄÊ¨°Áä∂ÊÄÅ
          try {
            const response = await getCoupleStatus();
            if (response && response.data) {
              if (response.data.isBound) {
                // Êõ¥Êñ∞Êú¨Âú∞‰ø°ÊÅØ
                saveCoupleInfo({
                  isBound: true,
                  coupleId: response.data.coupleId,
                  partnerId: response.data.partnerInfo?.userId || '',
                  partnerInfo: response.data.partnerInfo || {},
                  bindTime: response.data.bindTime || '',
                  role: response.data.role || ''
                });
                this.partnerInfo = response.data.partnerInfo || {};
                this.bindTime = response.data.bindTime || '';
              } else {
                // ÊúçÂä°Âô®ËøîÂõûÊú™ÁªëÂÆöÔºåÊ∏ÖÈô§Êú¨Âú∞Áä∂ÊÄÅ
                console.log('‚ö†Ô∏è ÊúçÂä°Âô®ËøîÂõûÊú™ÁªëÂÆöÔºåÊ∏ÖÈô§Êú¨Âú∞Áä∂ÊÄÅ');
                clearCoupleInfo();
                this.isBound = false;
                this.partnerInfo = {};
                this.bindTime = '';
              }
            }
          } catch (e) {
            console.error('ÂêåÊ≠•ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
          }
          return;
        }
        
        // Êú¨Âú∞Ê≤°ÊúâÔºåÊü•ËØ¢ÊúçÂä°Âô®
        this.isBound = false;
        try {
          const response = await getCoupleStatus();
          if (response && response.data) {
            if (response.data.isBound) {
              this.isBound = true;
              this.partnerInfo = response.data.partnerInfo || {};
              this.bindTime = response.data.bindTime || '';
              
              // ‰øùÂ≠òÂà∞Êú¨Âú∞
              saveCoupleInfo({
                isBound: true,
                coupleId: response.data.coupleId,
                partnerId: response.data.partnerInfo?.userId || '',
                partnerInfo: response.data.partnerInfo || {},
                bindTime: response.data.bindTime || '',
                role: response.data.role || ''
              });
            } else {
              // ÊúçÂä°Âô®ËøîÂõûÊú™ÁªëÂÆöÔºåÁ°Æ‰øùÊú¨Âú∞‰πüÊòØÊú™ÁªëÂÆöÁä∂ÊÄÅ
              this.isBound = false;
              this.partnerInfo = {};
              this.bindTime = '';
              clearCoupleInfo();
            }
          }
        } catch (e) {
          console.error('Êü•ËØ¢ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
          // Â¶ÇÊûúÊü•ËØ¢Â§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞Áä∂ÊÄÅ
          this.isBound = checkIsBound();
          if (this.isBound) {
            this.partnerInfo = getPartnerInfo() || {};
          }
        }
      } catch (e) {
        console.error('Ê£ÄÊü•ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
        this.isBound = checkIsBound();
        if (this.isBound) {
          this.partnerInfo = getPartnerInfo() || {};
        }
      }
    },
    
    // Â§ÑÁêÜÈÇÄËØ∑Á†ÅÔºà‰ªéÂàÜ‰∫´ËøõÂÖ•Ôºâ
    async handleInviteCode(code) {
      this.isInviteMode = true;
      this.isAccepting = false;
      
      try {
        uni.showLoading({ title: 'È™åËØÅ‰∏≠...' });
        
        const response = await validateInviteCode(code);
        uni.hideLoading();
        
        const isLegacySuccess = response && response.success && response.data;
        const isMsgCodeSuccess = this.isMsgCodeSuccess(response);
        const successMessage = this.getResponseMessage(response, 'ÈÇÄËØ∑Á†ÅÈ™åËØÅÊàêÂäü');

        if (isLegacySuccess || isMsgCodeSuccess) {
          const normalizedCreator = response?.data?.creator || response?.creator || {};
          const normalizedExpireAt = response?.data?.expireAt || response?.expireAt || '';
          const normalizedCode = response?.data?.code || code;

          this.creatorInfo = normalizedCreator;
          this.inviteCode = normalizedCode;
          this.expireAt = normalizedExpireAt;

          if (successMessage) {
            uni.showToast({
              title: successMessage,
              icon: 'success'
            });
          }
        } else {
          // Êèê‰æõÊõ¥ËØ¶ÁªÜÁöÑÈîôËØØÊèêÁ§∫
          const errorMsg = response?.msg || response?.message || 'ÈÇÄËØ∑Á†ÅÊó†ÊïàÊàñÂ∑≤ËøáÊúü';
          uni.showModal({
            title: 'È™åËØÅÂ§±Ë¥•',
            content: errorMsg + '\n\nÂèØËÉΩÂéüÂõ†Ôºö\n1. ÈÇÄËØ∑Á†Å‰∏çÂ≠òÂú®\n2. ÈÇÄËØ∑Á†ÅÂ∑≤ËøáÊúü\n3. ÈÇÄËØ∑Á†ÅÂ∑≤Ë¢´‰ΩøÁî®\n\nËØ∑Á°ÆËÆ§ÈÇÄËØ∑Á†ÅÊòØÂê¶Ê≠£Á°Æ',
            showCancel: false,
            success: () => {
              this.goBack();
            }
          });
        }
      } catch (error) {
        uni.hideLoading();
        console.error('È™åËØÅÈÇÄËØ∑Á†ÅÂ§±Ë¥•', error);
        
        // Êèê‰æõÊõ¥ËØ¶ÁªÜÁöÑÈîôËØØÊèêÁ§∫
        let errorMessage = 'È™åËØÅÈÇÄËØ∑Á†ÅÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•';
        if (error.message) {
          if (error.message.includes('ÈÇÄËØ∑Á†ÅÊó†Êïà') || error.message.includes('Êó†Êïà')) {
            errorMessage = 'ÈÇÄËØ∑Á†ÅÊó†Êïà\n\nÂèØËÉΩÂéüÂõ†Ôºö\n1. ÈÇÄËØ∑Á†Å‰∏çÂ≠òÂú®\n2. ÈÇÄËØ∑Á†ÅÂ∑≤ËøáÊúü\n3. ÈÇÄËØ∑Á†ÅÂ∑≤Ë¢´‰ΩøÁî®\n\nËØ∑Á°ÆËÆ§ÈÇÄËØ∑Á†ÅÊòØÂê¶Ê≠£Á°Æ';
          } else {
            errorMessage = error.message || errorMessage;
          }
        }
        
        uni.showModal({
          title: 'ÈîôËØØ',
          content: errorMessage,
          showCancel: false,
          success: () => {
            this.goBack();
          }
        });
      }
    },
    
    // ÁîüÊàêÈÇÄËØ∑Á†Å
    async generateInviteCode() {
      // ÂÖàÊ£ÄÊü•ÊòØÂê¶Â∑≤ÁªëÂÆö
      if (this.isBound) {
        uni.showToast({ title: 'ÊÇ®Â∑≤ÁªèÁªëÂÆö‰∫ÜÊÉÖ‰æ£ÂÖ≥Á≥ª', icon: 'none' });
        return;
      }
      
      this.isGenerating = true;
      try {
        const response = await createInviteCode();
        const legacySuccess = response && response.success && response.data;
        const msgCodeSuccess = this.isMsgCodeSuccess(response);
        
        if (legacySuccess || msgCodeSuccess) {
          this.inviteCode = response.data.inviteCode || '';
          this.expireAt = response.data.expireAt || '';
          
          // ‰øùÂ≠òÂà∞Êú¨Âú∞ÔºàÊú™ÁªëÂÆöÁä∂ÊÄÅ‰πü‰øùÂ≠òÈÇÄËØ∑Á†ÅÔºâ
          saveCoupleInfo({
            isBound: false,
            coupleId: '',
            partnerId: '',
            partnerInfo: null,
            bindTime: '',
            role: 'initiator',
            inviteCode: this.inviteCode,
            inviteCodeExpire: this.expireAt
          });
          
          uni.showToast({ title: this.getResponseMessage(response, 'ÈÇÄËØ∑Á†ÅÁîüÊàêÊàêÂäü'), icon: 'success' });
        } else {
          uni.showToast({ title: this.getResponseMessage(response, 'ÁîüÊàêÂ§±Ë¥•'), icon: 'none' });
        }
      } catch (error) {
        console.error('ÁîüÊàêÈÇÄËØ∑Á†ÅÂ§±Ë¥•', error);
        uni.showToast({ title: 'ÁîüÊàêÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', icon: 'none' });
      } finally {
        this.isGenerating = false;
      }
    },
    
    // ÈáçÊñ∞ÁîüÊàêÈÇÄËØ∑Á†Å
    async regenerateInviteCode() {
      uni.showModal({
        title: 'ÊèêÁ§∫',
