            console.error('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
            console.error('üî¥ [Â§±Ë¥•ÂéüÂõ†]', err);
            console.error('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
            // Â¶ÇÊûú showModal Â§±Ë¥•Ôºå‰ΩøÁî® showToast ‰Ωú‰∏∫Â§áÈÄâ
            uni.showToast({
              title: errorMessage.length > 20 ? errorMessage.substring(0, 20) + '...' : errorMessage,
              icon: 'none',
              duration: 3000,
              success: () => {
                console.log('‚úÖ [È°µÈù¢ÈîôËØØÂ§ÑÁêÜ] Â∑≤‰ΩøÁî®ToastÊòæÁ§∫ÈîôËØØ');
              },
              fail: (toastErr) => {
                console.error('‚ùå [È°µÈù¢ÈîôËØØÂ§ÑÁêÜ] Toast‰πüÂ§±Ë¥•:', toastErr);
              }
            });
          }
        });
      } finally {
        this.isVerifying = false;
      }
    },
    
    // ÂàÜ‰∫´ÈÇÄËØ∑
    shareInvite() {
      // Â∞èÁ®ãÂ∫èÂàÜ‰∫´ÈÄöËøáÂè≥‰∏äËßíËèúÂçïËß¶Âèë onShareAppMessage
      // ËøôÈáåÂèØ‰ª•ÊèêÁ§∫Áî®Êà∑Â¶Ç‰Ωï‰ΩøÁî®ÂàÜ‰∫´ÂäüËÉΩ
      uni.showModal({
        title: 'ÂàÜ‰∫´ÈÇÄËØ∑',
        content: 'ËØ∑ÁÇπÂáªÂè≥‰∏äËßí"..."ÊåâÈíÆÔºåÈÄâÊã©"ËΩ¨Âèë"ÂàÜ‰∫´ÁªôÂ•ΩÂèã',
        showCancel: false
      });
    },
    
    // Êé•ÂèóÈÇÄËØ∑
    async handleAccept() {
      if (!this.inviteCode) {
        uni.showToast({ title: 'ÈÇÄËØ∑Á†ÅÊó†Êïà', icon: 'none' });
        return;
      }
      
      // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªëÂÆö
      if (this.isBound) {
        uni.showModal({
          title: 'ÊèêÁ§∫',
          content: 'ÊÇ®Â∑≤ÁªèÁªëÂÆö‰∫ÜÊÉÖ‰æ£ÂÖ≥Á≥ªÔºåÊó†Ê≥ïÊé•ÂèóÊñ∞ÁöÑÈÇÄËØ∑',
          showCancel: false
        });
        return;
      }
      
      this.isAccepting = true;
      try {
        const response = await acceptInvite(this.inviteCode);
        const msgCodeSuccess = this.isMsgCodeSuccess(response);
        
        if ((response && response.success && response.data) || msgCodeSuccess) {
          // ‰øùÂ≠òÁªëÂÆö‰ø°ÊÅØ
          const coupleData = {
            isBound: true,
            coupleId: response.data.coupleId || '',
            partnerId: response.data.partnerInfo?.userId || '',
            partnerInfo: response.data.partnerInfo || {},
            bindTime: response.data.bindTime || '',
            role: 'accepter'
          };
          
          saveCoupleInfo(coupleData);
          
          uni.showToast({ 
            title: this.getResponseMessage(response, 'ÁªëÂÆöÊàêÂäüÔºÅ'), 
            icon: 'success',
            duration: 2000
          });
          
          // Âª∂ËøüË∑≥ËΩ¨ÔºåËÆ©Áî®Êà∑ÁúãÂà∞ÊàêÂäüÊèêÁ§∫
          setTimeout(() => {
            uni.reLaunch({
              url: '/pages/we/index'
            });
          }, 2000);
        } else {
          uni.showToast({ 
            title: this.getResponseMessage(response, 'Êé•ÂèóÂ§±Ë¥•'), 
            icon: 'none' 
          });
          this.isAccepting = false;
        }
      } catch (error) {
        console.error('Êé•ÂèóÈÇÄËØ∑Â§±Ë¥•', error);
        uni.showToast({ 
          title: error.message || 'Êé•ÂèóÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', 
          icon: 'none' 
        });
        this.isAccepting = false;
      }
    },
    
    // Ëß£ÁªëÂÖ≥Á≥ª
    async handleUnbind() {
      uni.showModal({
        title: 'Á°ÆËÆ§Ëß£Áªë',
        content: 'Ëß£Èô§ÂÖ≥Á≥ªÂêéÔºåÂèåÊñπÂ∞ÜÊó†Ê≥ïÂÖ±‰∫´Êï∞ÊçÆ„ÄÇÁ°ÆÂÆöË¶ÅËß£Èô§ÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'Ëß£Áªë‰∏≠...' });
              await unbindCouple();
              uni.hideLoading();
              
              // Ê∏ÖÈô§Êú¨Âú∞‰ø°ÊÅØ
              clearCoupleInfo();
              
              uni.showToast({ 
                title: 'Â∑≤Ëß£Èô§ÂÖ≥Á≥ª', 
                icon: 'success' 
              });
              
              // Êõ¥Êñ∞È°µÈù¢Áä∂ÊÄÅ
              this.isBound = false;
              this.partnerInfo = {};
              this.bindTime = '';
              
              // Âª∂ËøüÂà∑Êñ∞È°µÈù¢
              setTimeout(() => {
                this.checkCoupleStatus();
              }, 1500);
            } catch (error) {
              uni.hideLoading();
              console.error('Ëß£ÁªëÂ§±Ë¥•', error);
              uni.showToast({ 
                title: 'Ëß£ÁªëÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', 
                icon: 'none' 
              });
            }
          }
        }
      });
    },
    
    goBack() {
      uni.navigateBack();
    }
  }
};
</script>

